/* Documentation site component styles */
.componentPage--description {
  color: #444444;
  margin: 35px 0;
}

.clear {
  clear: both;
}

.componentPage--heading1 {
  font-size: 38px;
  display: block;
  position: relative;
  padding-bottom: 1.125rem;
  margin: 0 0 35px;
  font-weight: 700;
}

.componentPage--heading1:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 4.5rem;
  height: 4px;
  background-color: #00adf7;
  border-radius: 4px;
}

.componentPage--heading2 {
  display: block;
  position: relative;
  margin: 0 0 10px;
}

.componentPage--heading3 {
  border-bottom: thin solid #d3d3d3;
  padding-bottom: 0.375rem;
  margin-bottom: 1.5rem;
}

.example--heading4 {
  margin: 1rem 0 0.4rem 0.3rem;
}

.example {
  border: solid 1px #d3d3d3;
  border-radius: 3px;
}

.example h4:first-of-type {
   margin-top: 0;
}

.example p:last-of-type {
   margin-bottom: 0;
}

.exampleComponent {
  padding: 15px;
  margin-bottom: 3px; 
}

.examplewrapper {
  position: relative;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.1);
}

.button {
  position: absolute;
  color: #1978c8;
  right: 0;
  bottom: -26px;
  cursor: pointer;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  border: 1px solid #d3d3d3;
  border-top: 0;
  padding: 6px 8px;
  font-size: 14px;
  line-height: 1;
  text-decoration: none;
}

.button:hover {
  color: #c86919;
}

.codewrapper {
  background: #f5f5f5;
  max-height: 0;
}

.codewrapper-enter {
  opacity: 0;
  max-height: 0;
}

.codewrapper-enter-active {
  opacity: 1;
  transition: all 300ms ease-in;
  max-height: 100%;
}

.codewrapper-enter-done {
  opacity: 1;
  max-height: 100%;
}

.codewrapper-exit {
  opacity: 1;
  transition: all 300ms ease-out;
  max-height: 100%;
}

.codewrapper-exit-active {
  opacity: 0;
  transition: all 300ms ease-out;
  max-height: 0;
}

.importstatement {
  color: #999;
  font-family: Consolas,Liberation Mono,Menlo,monospace;
  font-size: 13px;
  margin-bottom: 35px;
}

.props {
  font-weight: 500;
}

.props tr td:nth-last-of-type(3) {
  text-align: center;
}

.props th, td {
  text-align: left;
  padding: 5px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.props td:last-of-type {
  width: 100%;
}

.navigation {
  float: left;
  width: 200px;
  list-style-type: none;
  padding-left: 20px;
}

.navigation--search {
  padding: 16px;
  margin-right: 25px;
}

.navigation--input {
  display: block;
  width: 100%;
  padding: 8px;
  color: #444;
  background-color: #fff;
  font-size: 15px;
  border: 1px rgb(240, 243, 245) solid;
  border-radius: 3px;
  transition: border-color ease-in-out .15s;
}

.navigation--input:focus {
  border-color: #07a;
  border: 1px #07a solid;
  outline: 0;
}

.navigation--listItem {
  margin: 8px 0 8px 0;
  font-size: 15px;
}

.navigation--link {
  text-decoration: none;
}

.navmenu {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 250px;
  overflow: auto;
  background-color: #07a;
  border-right: 1px solid rgba(0, 0, 0, 0.12);
}

.navmenu h1:first-of-type {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  display: block;
  padding: 15px;
  border-bottom: 1px solid #e8e8e8;
  color: #fcfcfc;
  text-align: center;
}

.navmenu li > a {
  color: #fff;
  font-weight: 500;
}

.propname {
  color: #07a;
}

.proptype {
  color: #1bab6b;
}

.main {
  max-width: 1000px;
  padding: 15px 32px;
  margin: 0 auto 50px auto;
}

.doc {
  padding-left: 250px;
}

.availableIcons--container {
  margin-right: 2rem;
}

.availableIcons--preview {
  padding: 16px;
  border: 1px #ededed solid;
  border-radius: 3px;
  display: inline-block;
  margin: 0 15px 15px 0;
}

.icon-spin-test {
  animation: spin 1s linear infinite;
}

/* @keyframes spin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } } */
